import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { AngularFireModule } from 'angularfire2';
import { FIREBASE_CONFIG } from './app.firebase.config';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { MyApp } from './app.component';


@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/cadastro/cadastro.module.ngfactory#CadastroPageModuleNgFactory', name: 'CadastroPage', segment: 'cadastro', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cartao/cartao.module.ngfactory#CartaoPageModuleNgFactory', name: 'CartaoPage', segment: 'cartao', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ficha-medica/ficha-medica.module.ngfactory#FichaMedicaPageModuleNgFactory', name: 'FichaMedicaPage', segment: 'ficha-medica', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/historico/historico.module.ngfactory#HistoricoPageModuleNgFactory', name: 'HistoricoPage', segment: 'historico', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#MedicosPageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/medicos/medicos.module.ngfactory#MedicosPageModuleNgFactory', name: 'MedicosPage', segment: 'medicos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/resetar-senha/resetar-senha.module.ngfactory#ResetarSenhaPageModuleNgFactory', name: 'ResetarSenhaPage', segment: 'resetar-senha', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sobre/sobre.module.ngfactory#SobrePageModuleNgFactory', name: 'SobrePage', segment: 'sobre', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



